.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
  color: black;
}

.nav-links a {
  border: 1px solid transparent;
  border-radius: 10px;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
  background-color: #12cbc4;
  color: black;
  box-shadow: 2px 2px 5px 2px red;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  /* background: #f8df00; */
  /* border-color: #292929; */
  color: #292929;
  background-color: #cd84f1;
  font-family: "Lucida Console", "	Brush Script MT", "Times New Roman";
  box-shadow: 2px 2px 5px 2px brown;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  border-radius: 10px;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: white;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: red;
    border-radius: 10px;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: red;
    color: #292929;
    box-shadow: 2px 2px 5px 2px #b53471;
  }
}
